import React from 'react';
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom'
import Loadable from 'react-loadable'

const Loading = () => <div>Loading...</div>;

const routes = [
  { name: 'Home', path: '/' },
  { name: 'RandomWalker', path: '/random-walker' },
]

const routingElements = routes.map((route, i) => {
  const loadable = Loadable({
    loader: () => import(`./routes/${route.name}`),
    loading: Loading,
  })
  return <Route key={i} exact path={`${process.env.PUBLIC_URL}${route.path}`} component={loadable}/>
})

const menu = () => {
  const items = routes.map((route, i) => {
    return (
      <li key={i}>
        <Link to={`${process.env.PUBLIC_URL}${route.path}`}>{route.name}</Link>
      </li>
    )
  })
  return (
    <ul>
      {items}
    </ul>
  )
}

const App = () => (
  <Router>
    <div>
      {menu()}
      {routingElements.map(route => route)}
    </div>
  </Router>
)

export default App



